import * as React from "react";
import AppBar from "@mui/material/AppBar";
import Box from "@mui/material/Box";
import Toolbar from "@mui/material/Toolbar";
import Typography from "@mui/material/Typography";

import { Stack } from "@mui/material";

import { useSelector } from "react-redux";

import { selectIdsOfItemsInCart } from "../features/metadataExplorer/metadataExplorerSlice";

import CartModal from "./CartModal";

export default function ButtonAppBar() {
  const idsOfItemsInCart = useSelector(selectIdsOfItemsInCart);

  const numItemsInCart = idsOfItemsInCart.length;
  let message = "";
  if (numItemsInCart === 1) {
    message = "1 item in cart";
  } else {
    message = `${numItemsInCart} items in cart`;
  }
  return (
    <Box sx={{ flexGrow: 1 }}>
      <AppBar position="static">
        <Toolbar>
          <Typography variant="h6" component="div" sx={{ flexGrow: 1 }}>
            Data Portal Demo
          </Typography>

          <Box>
            <Stack
              spacing={2}
              direction="row"
              sx={{
                display: {
                  xs: "none",
                  md: "flex",
                  flexDirection: "row",
                  alignItems: "center",
                },
              }}
            >
              <Typography>{message}</Typography>
              <CartModal></CartModal>
            </Stack>
          </Box>
        </Toolbar>
      </AppBar>
    </Box>
  );
}
