import { useState, Fragment } from "react";

import Box from "@mui/material/Box";
import Modal from "@mui/material/Modal";
import Button from "@mui/material/Button";
import IconButton from "@mui/material/IconButton";
import { Grid } from "@mui/material";

import Card from "@mui/material/Card";
import CardActions from "@mui/material/CardActions";
import CardContent from "@mui/material/CardContent";
import Typography from "@mui/material/Typography";

import DownloadForOfflineIcon from "@mui/icons-material/DownloadForOffline";
import Tooltip from "@mui/material/Tooltip";

import axios from "axios";

import { useSelector } from "react-redux";

import { CSVLink } from "react-csv";

import { selectIdsOfItemsInCart } from "../../../../features/metadataExplorer/metadataExplorerSlice";

const style = {
  position: "absolute",
  top: "50%",
  left: "50%",
  transform: "translate(-50%, -50%)",
  bgcolor: "background.paper",
  border: "2px solid #000",
  boxShadow: 24,
  pt: 2,
  px: 4,
  pb: 3,
};

function ChildModal() {
  const idsOfItemsInCart = useSelector(selectIdsOfItemsInCart);

  const [open, setOpen] = useState(false);
  const handleOpen = () => {
    setOpen(true);
  };
  const handleClose = () => {
    setOpen(false);
    setCsvFileCreated(false);
    setDataForCsv([]);
  };

  const [csvFileCreated, setCsvFileCreated] = useState(false);
  const [dataForCsv, setDataForCsv] = useState([]);

  const handleCreateFileClick = () => {
    console.log({ idsOfItemsInCart });

    axios
      .post("api/data/getSelectedVariables", idsOfItemsInCart)
      .then(function (response) {
        setDataForCsv(response.data);
        setCsvFileCreated(true);
      })
      .catch(function (error) {
        console.log(error);
      });
  };

  const renderButtonToDownloadCSV = () => {
    return (
      <CSVLink data={dataForCsv} filename={"data.csv"}>
        <Button size="small">Download CSV File</Button>
      </CSVLink>
    );
  };
  return (
    <Fragment>
      <Tooltip title="Download files related to cart contents">
        <IconButton onClick={handleOpen}>
          <DownloadForOfflineIcon></DownloadForOfflineIcon>
        </IconButton>
      </Tooltip>
      <Modal
        hideBackdrop
        open={open}
        onClose={handleClose}
        aria-labelledby="child-modal-title"
        aria-describedby="child-modal-description"
      >
        <Box sx={{ ...style }}>
          <h2 id="child-modal-title">
            Download files related to cart contents
          </h2>

          <Grid container spacing={1}>
            <Grid item xs={12}>
              <Card>
                <CardContent>
                  <Typography variant="h5" color="text.secondary" gutterBottom>
                    Data
                  </Typography>

                  <Typography
                    sx={{ mb: 1.5 }}
                    color="text.secondary"
                  ></Typography>
                  <Typography variant="body2">
                    Generate a CSV file containing the variables you have
                    selected and then download it.
                  </Typography>

                  <CardActions>
                    <Button size="small" onClick={handleCreateFileClick}>
                      Generate CSV File
                    </Button>
                    {csvFileCreated ? renderButtonToDownloadCSV() : null}
                  </CardActions>
                </CardContent>
              </Card>
            </Grid>
          </Grid>

          <Button onClick={handleClose}>Return to Cart</Button>
        </Box>
      </Modal>
    </Fragment>
  );
}

export default ChildModal;
